@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensans/opensans-300.woff2") format("woff2"), url("../fonts/opensans/opensans-300.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans/opensans-400.woff2") format("woff2"), url("../fonts/opensans/opensans-400.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/opensans/opensans-600.woff2") format("woff2"), url("../fonts/opensans/opensans-600.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensans/opensans-700.woff2") format("woff2"), url("../fonts/opensans/opensans-700.woff") format("woff"); }

/* -- ОБЩИЕ -- */
*,
*::after,
*::before {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  letter-spacing: -0.005em;
  background-color: #ffffff; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0; }

.nowrap {
  white-space: nowrap; }

/* -- CONTAINER -- */
.container {
  max-width: 1200px;
  padding: 0 30px;
  margin: 0 auto;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .container {
      max-width: 100%;
      padding: 0 0; } }

/* --- Section Title --- */
.section-title {
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  letter-spacing: -0.005em;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  color: #1f1f1f;
  /* -- TABLET -- */ }
  .section-title--light {
    color: #ffffff; }
  @media (max-width: 1023px) {
    .section-title {
      font-size: 24px;
      line-height: 41px; } }

/* --- HEADER --- */
.header {
  display: flex;
  flex-direction: column;
  min-height: 825px;
  padding-bottom: 50px;
  background: #000000 url("../img/header_bg_desktop_1x.jpg") center top no-repeat;
  background-size: cover;
  /* -- RETINA -- */
  /* -- TABLET -- */ }
  @media (min-resolution: 1.5dppx) {
    .header {
      background-image: url("../img/header_bg_desktop_2x.jpg"); } }
  @media (max-width: 1023px) {
    .header {
      position: relative;
      background-image: none;
      background-color: #ffffff; } }

.header__burger {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10; }

.header__top {
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__top {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 5;
      min-height: 568px;
      width: 100%;
      height: 100%;
      padding-top: 100px;
      background-color: #ffffff; }
      .header__top--tablet-hidden {
        display: none; }
      .header__top--tablet-visible {
        display: block; } }

.header__top-inner {
  width: 77.02%;
  min-width: 800px;
  margin: 0 auto;
  min-height: 65px;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__top-inner {
      width: 100%;
      min-width: 100%;
      margin: 0; } }

.header__nav {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.header__site-nav {
  margin: 0; }

.header__middle {
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  @media (max-width: 1023px) {
    .header__middle {
      position: absolute;
      left: 21px;
      top: 25px;
      order: -1; } }
  @media (max-width: 767px) {
    .header__middle {
      left: 19px; } }

.header__middle-inner {
  width: 77.02%;
  min-width: 800px;
  margin: 0 auto;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__middle-inner {
      width: 100%;
      min-width: 100%;
      margin: 0; } }

.header-logo {
  position: relative;
  bottom: -252px;
  display: block;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header-logo {
      bottom: 0; } }

.header__bottom {
  padding-top: 228px;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__bottom {
      padding-top: 0; } }

.header__bottom-inner {
  display: flex;
  justify-content: space-between;
  width: 77.02%;
  min-width: 800px;
  margin: 0 auto;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__bottom-inner {
      flex-direction: column;
      width: 100%;
      min-width: 100%;
      margin: 0; } }

.header__bottom-left {
  width: 420px;
  padding-top: 53px;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  @media (max-width: 1023px) {
    .header__bottom-left {
      width: 100%;
      min-height: 340px;
      padding: 134px 0 30px;
      background: #000000 url("../img/header_bg_tablet_1x.jpg") center center no-repeat;
      background-size: cover;
      /* -- RETINA -- */ } }
  @media (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .header__bottom-left {
      background-image: url("../img/header_bg_tablet_2x.jpg"); } }
  @media (max-width: 767px) {
    .header__bottom-left {
      min-height: 390px;
      padding-top: 121px;
      background-image: url("../img/header_bg_mobile_1x.jpg");
      /* -- RETINA -- */ } }
  @media (max-width: 767px) and (min-resolution: 1.5dppx) {
    .header__bottom-left {
      background-image: url("../img/header_bg_mobile_2x.jpg"); } }

.header__slogan-wrapper {
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__slogan-wrapper {
      width: 56.64%;
      min-width: 288px;
      margin: 0 auto;
      text-align: center; } }

.header__page-title {
  width: 435px;
  margin: 0 auto;
  padding-bottom: 24px;
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  color: #ffffff;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__page-title {
      width: auto;
      font-size: 30px;
      line-height: 42px; } }

.header__slogan {
  margin: 0;
  padding: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.8);
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__slogan {
      font-size: 18px;
      line-height: 32px; } }

.header__bottom-right {
  width: 327px;
  padding-top: 5px;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__bottom-right {
      align-self: center;
      width: 41.797%;
      min-width: 288px;
      padding-top: 70px; } }

/* -- ГЛАВНАЯ НАВИГАЦИЯ -- */
.site-nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0;
  padding-top: 22px;
  padding-left: 19px;
  padding-right: 22px;
  padding-bottom: 10px;
  margin-left: -20px;
  list-style: none;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .site-nav {
      flex-direction: column;
      padding: 0;
      margin-left: 0; } }

.site-nav li {
  margin-left: 20px;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .site-nav li {
      margin-left: 0;
      background-color: #f6f6f6; }
      .site-nav li:not(:last-child) {
        margin-bottom: 30px; } }

.site-nav a {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  letter-spacing: -0.005em;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.7);
  transition: 0.3s;
  /* -- TABLET -- */ }
  .site-nav a:hover {
    opacity: 0.7; }
  .site-nav a:active {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    .site-nav a {
      display: block;
      position: relative;
      width: 100%;
      padding: 17px 35px 17px 17px;
      font-weight: 400;
      font-size: 20px;
      line-height: 28px;
      color: #48494d; }
      .site-nav a::after {
        position: absolute;
        content: "";
        top: 50%;
        transform: translateY(-50%);
        right: 25px;
        width: 8px;
        height: 12px;
        background: url("../img/menu_arrow.svg") no-repeat; } }

.burger {
  height: 50px;
  width: 50px;
  border-style: none;
  border: none;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat; }
  @media (min-width: 1024px) {
    .burger {
      display: none; } }
  .burger--close {
    background-image: url("../img/burger_close.svg");
    background-color: #ffffff; }
    .burger--close:hover {
      box-shadow: 0 0 5px 1px black; }
  .burger--open {
    background-image: url("../img/burger_open.svg");
    background-color: #0ad9c6; }
    .burger--open:hover {
      opacity: 0.7; }

/* -- Login Form -- */
.login-form__label {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  color: #ffffff;
  text-align: center;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  @media (max-width: 1023px) {
    .login-form__label {
      font-size: 18px;
      line-height: 28px;
      color: #000000;
      margin-bottom: 30px; } }
  @media (max-width: 767px) {
    .login-form__label {
      margin-bottom: 21px; } }

.login-form input {
  width: 100%;
  height: 48px;
  padding-top: 8px;
  padding-left: 29px;
  padding-right: 29px;
  margin-bottom: 20px;
  font-size: 15px;
  line-height: 24px;
  color: #1c1c1c;
  border: 1px solid #c9ccd4;
  border-radius: 4px;
  background-color: #ffffff;
  /* -- TABLET -- */ }
  .login-form input:placeholder {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    .login-form input {
      padding-top: 3px;
      padding-left: 23px;
      padding-right: 23px; } }

.login-form button[type="submit"] {
  width: 100%;
  padding: 15px 20px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  border: none;
  border-radius: 6px;
  background-color: #0ad9c6; }

/* --- ФАКТЫ О НАС --- */
.facts__list {
  display: flex;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #000000;
  list-style-type: none; }
